import { Directive, Input, HostBinding, HostListener, AfterContentInit } from '@angular/core';

const LINK_CLASSES = {
  underline: 'link-underline',
  underlineSmall: 'link-underline-small',
  underlineDefault: 'link-underline-default',
  underlineLarge: 'link-underline-large',
  simple: 'bc-link',
  large: 'link-large',
  small: 'link-small',
  default: 'link-default'
};

/**
 * Componente links se usa para indicar un vínculo a diferentes vistas.
 *
 * @export
 * @class BcLinkDirective
 * @implements {AfterContentInit}
 */
@Directive({
  selector: '[bc-link]'
})
export class BcLinkDirective implements AfterContentInit {


  /**
   * Estilo de link.
   *
   * @type {('simple' | 'underline')}
   * @memberof BcLinkDirective
   */
  @Input() typeLink: 'simple' | 'underline' = 'simple';
  /**
   * Tamaño del link.
   *
   * @type {('default' | 'small' | 'large' | 'underlineSmall' | 'underlineLarge' | 'underlineDefault')}
   * @memberof BcLinkDirective
   */
   @Input() sizeLink: 'default' | 'small' | 'large' | 'underlineSmall' | 'underlineLarge' | 'underlineDefault' = 'default';
  /**
   * Clase del link.
   *
   * @type {string}
   * @memberof BcLinkDirective
   */
  @Input() class = '';

  @HostBinding('class') clazzes = [
    'bc-link',
    this.class,
    this.getStyles(),
  ].join(' ');

  @HostListener('focusout') onFocusOut() {
    this.clazzes = this.clazzes.replace(this.setAccesibility(), '');
  }

  @HostListener('keyup', ['$event']) onFocus(event: KeyboardEvent) {
    if (event && event.key === 'Tab') {
      this.clazzes += ' ' + this.setAccesibility();
    }
  }

  /**
   * Metodo para la accesibilidad
   *
   * @returns {string}
   * @memberof BcLinkDirective
   */
  setAccesibility(): string {
    return 'outline-element';
  }
  ngAfterContentInit(): void {
    this.clazzes = [
      'bc-link',
      this.class,
      this.getStyles(),
      this.getSize(),
    ].join(' ');
  }

  /**
   * Obtener estilos por defecto
   *
   * @returns {string}
   * @memberof BcLinkDirective
   */
  getStyles(): string {
    return LINK_CLASSES[this.typeLink] || LINK_CLASSES.simple;
  }
  getSize(): string {
    return LINK_CLASSES[this.sizeLink] || LINK_CLASSES.default;
  }
}
